.popWrapper {
  margin-top: 20%;
  text-align: center;
  display: flex;
  flex-direction: column;
}

.balloonContainer {
  width: 80vw;
  height: 50vh;
  /* background-color:violet; */
  margin: 50px auto 25px auto;
}

.balloonWrapper {
  position: relative;
}

/* Balloon design */
.balloon {
  height: 130px;
  width: 104px;
  border-radius: 80% 80% 80% 80%;
  background-color: red;
  position: relative;
  margin: 0 5px;
}
.balloon:before {
  content: "";
  color: rgba(182, 15, 97, 0.9);
  position: absolute;
  bottom: -9px;
  left: 50%;
  transform: translateX(-50%);
  border-left: 5px solid transparent;
  border-right: 5px solid transparent;
  border-bottom: 10px solid red;
}

.balloon-ref {
  position: absolute;
  left: 0px;
  top: 0;
  /* width: 100%; */
  height: 80%;
  border-radius: 90%;
  background: rgb(255, 255, 255);
  background: radial-gradient(
    circle,
    rgba(255, 255, 255, 0.41015625) 0%,
    rgba(148, 214, 233, 0) 100%
  );
}
.balloon span.top-c {
  position: absolute;
  top: 0px;
  left: 20px;
  width: 15px;
  height: 40px;
  border-radius: 50%;
  background-color: rgba(255, 255, 255, 0.6);
  transform: rotate(48deg);
}
.balloon span.left-c {
  position: absolute;
  top: 36px;
  left: 6px;
  width: 7px;
  height: 7px;
  border-radius: 50%;
  background-color: rgba(255, 255, 255, 0.6);
  transform: rotate(48deg);
}